import { Injectable } from '@angular/core';
import {
  IViewDesignerInspectorConfig,
  RxViewCustomizationOptionsComponent,
  RxViewRevertCustomizationComponent,
  ViewDesignerFacade
} from '@helix/platform/view/designer';
import { IFormSectionBuilderConfig } from '@helix/platform/shared/api';

@Injectable()
export class RxShellModel {
  constructor(private viewDesignerFacade: ViewDesignerFacade) {
    this.viewDesignerFacade.setViewInspectorConfig(this.getInspector());
  }

  private getInspector(): IViewDesignerInspectorConfig {
    const layout: IFormSectionBuilderConfig[] = [
      {
        label: 'General',
        controls: [
          {
            component: RxViewRevertCustomizationComponent
          },
          {
            component: RxViewCustomizationOptionsComponent
          }
        ]
      }
    ];

    return {
      inspectorSectionConfigs: layout
    } as IViewDesignerInspectorConfig;
  }
}
